<template>
	<div id="send-gift-list">
		<commonHeader :title="'选择礼物'"></commonHeader>
		<div class="gift__tab">
      <span data-id='1' class="tab" :class="{'active': active == 1}" @click="changeType(1)">实物礼物</span>
      <span data-id='2' class="tab" :class="{'active': active == 2}" @click="changeType(2)">定制礼物</span>
      <span data-id='3' class="tab" :class="{'active': active == 3}" @click="changeType(3)">虚拟礼物</span>
    </div>
    
    <template v-if="goodses.length == 0">
    	<div>暂无数据</div>
    </template>
    
    <template v-else>
    	<div class="list-wrap">
		    <div class="gift virtual" v-for="(item,index) in goodses" :key="index">
		      <div class="gift__check">
		        <input class="radio" type="radio" name="address"  @click="checked(item,index)"/>
		      </div>
		      <div class="gift__left">
		        <img :src="item.imagePath" alt="">
		      </div>
		      <div class="gift__right">
		        <div class="gift__right__name">{{item.name}}</div>
		        
		        <div class="gift__right__bottom">
		          <div class="bottom__price" v-if="active != '3'">￥{{item.price}}</div>
		          <div class="bottom__info">
		            <span class="user"  v-if="active != '3'">赠送者：{{item.senderId==0?'自购':item.senderName}}</span>
		            <span class="heart" v-if="active == '3'">
		              <span class="heart__icon"></span>
		              <span class="heart__num">{{item.price}}</span>
		            </span>
		            <span class="num">x{{item.num}}</span>
		          </div>
		        </div>
		      </div>
	    	</div>
    	</div>
    </template>
    
    <div class="gift__bottom">
      <div class="count">
        <span class="count__text">数量</span>
        <Counter :num="value" :max="num" @change="valChange"/>
      </div>
      <div class="submit" @click="confirmGift">确定</div>
    </div>
	</div>
</template>

<script>
import Counter from '@/components/Counter/Counter';
export default {
	data() {
      return {
        active: '1',
        value: 1,
        num:1,
        giftObj:'',
        giftType:1,
        goodses:[],
        PARAMS:{
        	pa:1,
        	li:9999,
        	ob:1,
        }
      }
    },
		created(){
      this.getGiftGoodsList(1)
		},
    methods: {
      changeType(type) {
      	this.active = type;
      	document.documentElement.scrollTop = 0
        this.goodses = []
				this.PARAMS = Object.assign({},this.PARAMS,{
					pa:1,
				})
      	switch(type){
          case 1:
          	this.getGiftGoodsList(1)
            this.giftType = 1
          break;
          case 2:
            this.giftType = 5
          	this.getGiftGoodsList(5)
          break;
          case 3:
            this.giftType = 21
          	this.getGiftGoodsList(21)
          break;
        }
      },

      //计算器
      valChange(val) {
        this.value += val;
     	},

      //当type = 3， 点击选中返回选中下标
      getCheckedIndex(index) {
        console.log(index)
      },
      //选中并返回选中下标
      checked(item,index) {
        this.$emit('checked', index);
        console.log(item,index)
        this.num = item.num
        this.giftObj = item
      },
      confirmGift(){
      	if(this.giftObj){
      		console.log(this.giftObj)
					this.$set(this.giftObj,'num', this.value)
					
					let giftStr = JSON.stringify(this.giftObj)
      		this.$router.push({name:'SendGift',query:{giftObj:giftStr,type:this.giftType}})
//    		localStorage.setItem('giftObj',giftStr)
      	}else{
      		this.$toast('请选择礼物!')
      	}
      },
      getGiftGoodsList(type){
      	this.$Api.getGiftGoodsList(type,this.PARAMS).then((res)=>{
      		console.log(res)
      		if(res.q.s == 0){
      			this.goodses = res.q.goodses
      		}
      	})
      },
    },
    
	  components: {
	      Counter,
	  },
  
}
</script>

<style lang="scss">
	@import "../../sass/baseSass.scss";
	#send-gift-list{
		height : 100%;
		background:#f0f0f0;
		.gift__tab {
			position: fixed;
    	top: 1.17rem;
    	left: 0;
    	z-index: 100;
    	width:100%;
      height: 1.067rem;
      line-height: 1.067rem;
      font-size: 0;
      background: #fff;
      border-bottom: 1px solid #eee;
      .tab {
        display: inline-block;
        width: 33.3%;
        text-align: center;
        font-size: 0.4rem;
        color: #333;
      }
      .tab.active {
        position: relative;
        color: #de7fef;

        &:after {
          content: '';
          position: absolute;
          left: 0;
          bottom: 0;
          height: 2px;
          width: 100%;
          background: #de7fef;
        }
      }
    }
    .list-wrap{
    	position: absolute;
    	top: 1.067+1.17rem;
    	bottom: 1.3067rem;
    	width: 100%;
    	overflow-y: auto;
    }
    .gift {
      padding: 0.267rem 0.4rem;
      height: 2.9333rem;
      background: #fff;
      box-sizing: content-box;
      border-bottom: 1px solid #eee;

      .gift__left {
        float: left;

        img {
          width: 2.9333rem;
          height: 2.9333rem;
        }
      }

      .gift__right {
        position: relative;
        float: right;
        width: 5.867rem;
        height: 2.9333rem;

        .gift__right__name {
          font-size: 0.3733rem;
        }

        .gift__right__bottom {
          position: absolute;
          bottom: 0;
          left: 0;
          width: 100%;

          .bottom__price {
            font-size: 0.453rem;
            font-weight: 600;
            color: #fe4436;
          }

          .bottom__info {
            position: relative;
            width: 100%;
            color: #666;

            span {
              display: inline-block;
            }

            .num {
              position: absolute;
              right: 0;
              bottom: 0;
              vertical-align: middle;
            }

            .heart {
              vertical-align: middle;
              .heart__icon {
                display: inline-block;
                width: 0.5067rem;
                height: 0.45333rem;
                background: url('../../assets/heart.png') no-repeat;
                background-size: 100%;
                background-position: center;
                vertical-align: middle;
              }

              .heart__num {
                color: #de7fef;
                vertical-align: middle;
              }
            }
          }
        }
      }
    }

    .virtual {
      .gift__right {
        width: 5.267rem;
      }

      .gift__check {
        float: left;
        display: flex;
        height: 100%;
        align-items: center;

        .radio{
          position: relative;
          -moz-appearance: none;
          -webkit-appearance: none;
          float: left;
          margin-right: 0.2rem;
          width: 0.533rem;
          height: 0.533rem;

          &::after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 0.533rem;
            height: 0.533rem;
            background: #fff;
            border: 1px solid #eee;
            border-radius: 50%;
            z-index: 9;
            box-sizing: border-box;
          }

          &:checked {
            &::after {
              border: none;
              background: url("../../assets/cart/ico_checked.png") no-repeat center;
              background-size: 100%;
            }
          }
        }

      }
    }
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    .gift__bottom {
      position: fixed;
      bottom: 0;
      left: 0;
      display: flex;
      justify-content: space-between;
      height: 1.3067rem;
      line-height: 1.3067rem;
      width: 100%;
      font-size: 0.453rem;
      background: #fff;
      border-top: 1px solid #eee;

      .count {
        display: flex;

        .count__text {
          padding: 0 0.6rem 0 0.4rem;
        }
      }

      .submit {
        padding: 0 1.07rem;
        color: #fff;
        background-image: linear-gradient(135deg, #fe9bba 0%, #de7fef 100%);
      }
    }
	}
</style>


